




















import Vue from "vue";
import Card from "../components/Card.vue";
export default Vue.extend({
	components: {
		Card,
	},
	methods: {
		quiz(type: string) {
			this.$store.commit("setQuizOption", {
				totalRound: this.$store.state.round,
				optionCnt: 5,
				type: type,
			});
			this.$router.push("/quiz");
		},
	},
});
